import React from 'react';
import { Menu } from 'antd';
const SubMenu = Menu.SubMenu;
const MenuItemGroup = Menu.ItemGroup;

function LeftMenu(props) {
  return (
    <Menu mode={props.mode} style={{backgroundColor:'black'}}>
    <Menu.Item key='intro' className='hoverToRed'>
      <a href="/intro" ></a>
    </Menu.Item>
    <Menu.Item key='hint' className='hoverToRed'>
      <a href="/hint" ></a>
    </Menu.Item>
    <SubMenu title={<a href="/forum" onClick={(e)=>{e.preventDefault(); }} ></a>} className='hoverToRed' >
        <Menu.Item key="setting:1" ><a href="/forum/free" className='submenu'></a></Menu.Item>
        <Menu.Item key="setting:2"><a href="/forum/playtip"  className='submenu'></a></Menu.Item>
        <Menu.Item key="setting:3" ><a href="/forum/idea" className='submenu'></a></Menu.Item>
    </SubMenu>
    <Menu.Item key='review' className='hoverToRed'>
      <a href="/review"></a>
    </Menu.Item>
    <Menu.Item key='report' className='hoverToRed'>
      <a href="/report"></a>
    </Menu.Item>
   
  </Menu>
  )
}

export default LeftMenu